@import url('https://fonts.googleapis.com/css2?family=Crimson+Pro:ital,wght@0,400;0,500;0,600;1,600&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Ubuntu+Mono&display=swap');

body {
  background-color: #fff0f0;
  padding: 0;
  font: 16.0px/1.5 Crimson Pro, serif;
  color: #4c7ca4;
  font-weight: 400;
  margin: 0;
}

.social-icons a {
  display: inline-block;
  height: 3rem;
  width: 3rem;
  background-color: #495057;
  color: #fff !important;
  border-radius: 100%;
  text-align: center;
  font-size: 1.5rem;
  line-height: 3.1rem;
  margin-right: 1rem;
}

.social-icons a:last-child {
  margin-right: 0;
}

.social-icons a:hover {
  background-color: #000000;
}

autocolor {
  color: #4c7ca4;
}

lightonly {
  visibility: hidden;
}

darkonly {
  visibility: visible;
}

education {
  font-weight: 500;
  font-size: 115%;
}

papertitle {
  font-weight: 600;
  font-size: 100%;
  color: #949cbc;
}

.image {
  border-radius: 0.35em;
  border: 0;
  display: inline-block;
  position: relative;
}

.image:before {
  -moz-transition: opacity 0.2s ease-in-out;
  -webkit-transition: opacity 0.2s ease-in-out;
  -ms-transition: opacity 0.2s ease-in-out;
  transition: opacity 0.2s ease-in-out;
  border-radius: 0.35em;
  content: '';
  display: block;
  height: 100%;
  left: 0;
  opacity: 0.5;
  position: absolute;
  top: 0;
  width: 100%;
}

.image.avatar {
  border-radius: 0%;
}

.image.avatar:before {
  display: none;
}

.image.avatar img {
  border-radius: 0%;
  width: 100%;
  // padding: 20px;
}

.image.logo img {
  border-radius: 0%;
  width: 60%;
  // padding: 20px;
}

#header .image.avatar {
  margin: 0 0 1em 0;
  width: 8.00em;
}

#header .image.logo {
  margin: 0 0 1em 0;
  width: 8.00em;
}

h1, h3, h4, h5, h6 {
  font-weight: 600;
  color: #4a7ba6;
  margin: 0 0 20px;
}

h2 {
  color: #bc84a4;
  margin: 2px 0px 15px;
  font-size: 157%;
}

p, ul, ol, table, pre, dl {
  margin: 0 0 20px;
}

h1, h2, h3 {
  line-height: 1.1;
}

h1 {
  font-size: 28px;
  color: #6676c4;
}

a {
  color: #949cbc;
  text-decoration: none;
}

a:hover {
  color: #bc84a4;
}

a small {
  font-size: 11px;
  color: #777;
  margin-top: -0.3em;
  display: block;
}

a:hover small {
  color: #777;
}

.wrapper {
  width: 960px;
  margin: 0 auto;
}

blockquote {
  border-left: 1px solid #e5e5e5;
  margin: 0;
  padding: 0 0 0 20px;
  font-style: italic;
}

code, pre {
  font-family: 'Ubuntu Mono', Monaco, Bitstream Vera Sans Mono, Lucida Console, Terminal, Consolas, Liberation Mono, DejaVu Sans Mono, Courier New, monospace;
  color: #949cbc;
}

pre {
  max-width: 500px;
  padding: 8px 15px;
  background: #f8f8f8;
  border-radius: 5px;
  border: 1px solid #e5e5e5;
  overflow-x: auto;
}

table {
  width: 100%;
  border-collapse: collapse;
}

th, td {
  text-align: left;
  padding: 5px 10px;
  border-bottom: 1px solid #e5e5e5;
}

dt {
  color: #bc84a4;
  font-weight: 500;
}

th {
  color: #bc84a4;
}

img {
  max-width: 100%;
}

header {
  width: 220px;
  float: left;
  position: fixed;
  -webkit-font-smoothing: subpixel-antialiased;
  padding-top: 4.0em;
  text-align: center;
}

header ul {
  list-style: none;
  height: 40px;
  padding: 0;
  background: #f4f4f4;
  border-radius: 5px;
  border: 1px solid #e0e0e0;
  width: 270px;
}

header li {
  width: 89px;
  float: left;
  border-right: 1px solid #e0e0e0;
  height: 40px;
}

header li:first-child a {
  border-radius: 5px 0 0 5px;
}

header li:last-child a {
  border-radius: 0 5px 5px 0;
}

header ul a {
  line-height: 1;
  font-size: 11px;
  color: #999;
  display: block;
  text-align: center;
  padding-top: 6px;
  height: 34px;
}

header ul a:hover {
  color: #999;
}

header ul a:active {
  background-color: #f0f0f0;
}

strong {
  color: #4a7ba6;
  font-weight: 2000;
}

header ul li + li + li {
  border-right: none;
  width: 89px;
}

header ul a strong {
  font-size: 14px;
  display: block;
}

section {
  width: 650px;
  float: right;
  padding-top: 4.0em;
  padding-bottom: 50px;
}

small {
  font-size: 11px;
}

hr {
  border: 0;
  background: #e5e5e5;
  height: 1px;
  margin: 0 0 20px;
}

footer {
  width: 220px;
  float: left;
  position: fixed;
  bottom: 30px;
  -webkit-font-smoothing: subpixel-antialiased;
  text-align: center;
}

@media print, screen and (max-width: 960px) {

  div.wrapper {
    width: auto;
    margin: 0;
  }

  header, section, footer {
    float: none;
    position: static;
    width: auto;
  }

  header {
    padding-right: 320px;
  }

  section {
    border: 3px solid #fcacbc;
    border-width: 5px 0;
    padding: 20px 0;
    margin: 0 0 20px;
  }

  header a small {
    display: inline;
  }

  header ul {
    position: absolute;
    right: 50px;
    top: 52px;
  }

  .image.avatar img {
    border-radius: 0%;
    width: 117px;
    padding: 20px;
  }

  .image.logo img {
    border-radius: 0%;
    width: 117px;
    padding: 20px;
  }

  #head1o2 {
    visibility: hidden;
  }

  #head2o2 {
    visibility: hidden;
  }
}

@media print, screen and (max-width: 960px) {
  body {
    word-wrap: break-word;
    padding: 15px;
  }

  header {
    padding: 0;
  }

  header ul, header p.view {}

  pre, code {
    word-wrap: normal;
  }
}

@media print, screen and (max-width: 480px) {
  body {
    padding: 15px;
  }

  header ul {
    width: 99%;
  }

  header li, header ul li + li + li {
    width: 33%;
  }

  .image.avatar img {
    border-radius: 100%;
    width: 200px;
    padding: 20px;
  }

  .image.logo img {
    border-radius: 100%;
    width: 100px;
    padding: 20px;
  }

  #head1o2 {
    visibility: hidden;
  }

  #head2o2 {
    visibility: hidden;
  }
}

@media print {
  body {
    padding: 0.4in;
    font-size: 12pt;
    color: #444;
  }
}

.biblink {
  text-decoration: none;
  display: inline;
  margin-right: 0px;
}

.fakelink {
  text-decoration: none;
  cursor: pointer;
}

.bibref {
  font-size: 70%;
  margin-top: 10px;
  margin-left: 0px;
  display: none;
  font-family: monospace;
}
